*{
  padding:0;
  margin:0;
  box-sizing: border-box;
  font-family: sans-serif;
}
.section{
  width:100vw;
  height:100vh;
  display: flex;
  flex-direction: column;
  place-items: center;
  position: relative;
  background:linear-gradient(to bottom,black,rgb(17, 6, 37) );
  justify-content: center;
  color:white;
  overflow: hidden;
}



.image{
  width:55%;
  height:80%;
  position: absolute;
  left:5%;
  filter: brightness(110%);
  overflow: hidden;
  transition: all 300ms linear;
}
.image > img{
  width:100%;
  height:100%;
  position:absolute;
  
  transition: all 300ms linear;
}
.section > h1{
  position: absolute;
  top:2%;
  left:5%;
  z-index: 33;
  font-size:5vmin;
}

.section > p{
  width:30%;
  height:80%;
  right:5%;
  position:absolute;
  top:8%;
  line-height: 5.5vmin;
  font-size:2vmin;
  font-weight: bold;
  padding:0vmin 2vmin 0vmin 2vmin;
  animation:para 700ms linear forwards;
}
.image > i{
  font-size:2vmin;
  position: absolute;
  left:5%;
  bottom:4%;
}
@keyframes para {
0%{
  opacity: 0;
  transform:translateX(150%);
}
100%{
  transform:translateX(0%);
  opacity: 10;
}
}

.section > a{
  position:absolute;
  bottom:2%;
  right:5%;
  width:25vmin;
  height:8vmin;
  background:rgb(36, 36, 36);
  color:rgb(255, 255, 255);
  text-decoration: none ;
  display: grid;
  place-items: center;
  font-size:2.1vmin;
  ;
}

@media only screen and (max-width:1080px){

  .section{
    height:150vh;
  }
  .image{
    width:100%;
    height:35%;
    left:0%;
    top:0%;
    overflow: hidden;
    animation: leg 1s linear forwards;
  }
 .section >  p{
    width:90%;
    height:60%;
    padding:2vmin 2vmin 0vmin 2vmin;
    font-size:3.5vmin;
    text-align: justify;
    top: 38%;
    line-height: 9vmin;
    animation: leg 1s linear forwards;
    overflow: scroll;
  }

  @keyframes leg{
    
    0%{
      transform: translateY(-50%);
      opacity: 0;
    }
    100%{
      transform: translateY(0%);
      opacity: 10;
    }
  }

  .section > p{
    animation-delay: 0.5s;
  }
  .section > .image{
    animation-delay: 1s;
  }
}